.navbar {
  height: 100%;
  width: 280px;
  display: flex;
  flex-direction: column;
  background-color: yellow;
  color: #111;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  transition: all 450ms ease-in-out;
}

.navbar.hidden {
  margin-left: -280px;
}

.navbar > div {
  display: flex;
  align-items: center;
  padding-left: 3vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  margin-top: 1vh;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

.navbar > div:hover {
  background-color: #ffffff87;
  padding-left: 3.5vw;
}

.navbar > div > a {
  text-decoration: none;
  color: #111;
  font-size: 1.08rem;
}
.navbar_icon {
  color: #111;
  transition: all 0.45s ease-in-out;
  margin-right: 1vw;
}

.navbar_icon:hover {
  transform: scale(1.08);
}

.close_button {
  cursor: pointer;
  align-self: flex-end;
  position: relative;
  top: -1vh;
  right: 1vw;
}

.close_button:hover {
  background-color: yellow !important;
}

@media only screen and (min-device-width : 320px) and (max-device-width : 480px) {
  .navbar > div{
    padding-left: 7vw;
  }

  .navbar > div:hover{
    padding-left: 8vw;
  }
  .navbar > div > a{
    font-size: 1rem;
    padding-left: 10px;
  }
}